import React from 'react'
import { graphql } from 'gatsby'
import { Container } from 'theme-ui'
import Layout from '@solid-ui-layout/Layout'
import Seo from '@solid-ui-components/Seo'
import Divider from '@solid-ui-components/Divider'
import Header from '@solid-ui-blocks/Classicesp/HeaderBlock01'
import Tabs from '@solid-ui-components/Tabs'
import Hero from '@solid-ui-blocks/Classicesp/HeroBlock01'
import FeatureOne from '@solid-ui-blocks/Classicesp/FeaturesWithPhotoBlock02'
import FeatureTwo from '@solid-ui-blocks/Classicesp/FeaturesWithPhotoBlock05'
import FeatureThree from '@solid-ui-blocks/Classicesp/FeaturesWithPhotoBlock01'
import Features from '@solid-ui-blocks/Classicesp/FeaturesBlock02'
import Footer from '@solid-ui-blocks/Classicesp/FooterBlock01'
import WhyChooseUs from '@solid-ui-blocks/Classicesp/FeaturesBlock04'
import Services from '@solid-ui-blocks/Classicesp/FeaturesBlock05'
import { normalizeBlockContentNodes } from '@blocks-helpers'
import { Helmet } from "react-helmet"
import theme from './_theme'
import styles from './_styles'

const IndexPage = props => {
  const { allBlockContent } = props.data
  const content = normalizeBlockContentNodes(allBlockContent?.nodes)

  return (
    <Layout theme={theme} {...props}>
      <Seo title='Software for Email Service Providers' description="Email service providers can have the Mumara ESP installed on their webserver, can configure it with all required resources to offer email services to its clients." />
      {/* Blocks */}
      <Header content={content['header']} menuJustify='space-between' />
      <Divider space='5' />
      <Container variant='full' sx={styles.heroContainer}>
        <Hero content={content['hero']} reverse />
      </Container>
      <Divider space='5' />
      <Divider space='5' />
      <Divider space='5' />
      <Divider space='5' />
      <WhyChooseUs content={content['why-choose-us']} />
      <Divider space='4' />
      <Container variant='wide' sx={styles.tabsContainer}>
        <Tabs space={5}>
          <FeatureOne content={content['tab-feature-one']} />
          <FeatureTwo content={content['tab-feature-two']} />
          <FeatureThree content={content['tab-feature-three']} />
        </Tabs>
      </Container>
      <Divider space='5' />
      <Services content={content['services']} />
      <Divider space='5' />
      <Footer content={content['footer']} />

      <Helmet>
        <script type="application/ld+json">
          {`
            {
              "@context": "http://schema.org/", 
              "@type": "BreadcrumbList", 
              "itemListElement": [
                { 
                  "@type": "ListItem", 
                  "position": "1", 
                  "item": { 
                    "@id": "https://www.mumara.com", 
                    "name": "Mumara",
                    "image": "https://mumara-new.netlify.app/assets/img/logo-dark.png" 
                  } 
                },{ 
                  "@type": "ListItem", 
                  "position": "2", 
                  "item": { 
                    "@id": "https://www.mumara.com/classic-esp/", 
                    "name": "Classic ESP"
                  } 
                }
              ]
            }
          `}
        </script>
        <script type="application/ld+json">
          {`
            {
              "@context": "http://schema.org/",
              "@type": "Product",
              "name": "Classic ESP",
              "image": [
                "https://mumara-new.netlify.app/assets/img/logo-dark.png"
              ],
              "description": "Email service providers can have the Mumara ESP installed on their webserver, can configure it with all required resources to offer email services to its clients.",
              "sku": "0446310777",
              "mpn": "925877",
              "brand": {
                "@type": "Thing",
                "name": "Mumara"
              },
              "review": {
                    "@type": "Review",
                    "reviewRating": {
                      "@type": "Rating",
                      "ratingValue": "4",
                      "bestRating": "5"
                    },
                    "author": {
                      "@type": "Person",
                      "name": "Fred Benson"
                    }
              },
              "aggregateRating": {
                "@type": "AggregateRating",
                "ratingValue": "5.0",
                "reviewCount": "1077"
              },
              "offers": {
                "@type": "Offer",
                "url": "https://www.mumara.com/classic-esp/",
                "priceCurrency": "USD",
                "price": "119.99",
                "priceValidUntil": "2020-11-20",
                "itemCondition": "https://schema.org/UsedCondition",
                "availability": "https://schema.org/InStock",
                "seller": {
                  "@type": "Organization",
                  "name": "Hostings House"
                }
              }
            }
          `}
        </script>
        <script type="application/ld+json">
          {`
            {
              "@context": "https://schema.org",
              "@type": "Organization",
              "name": "Mumara",
              "alternateName": "Marketing Automation &amp; Customer Engagement Tools | Mumara",
              "url": "https://www.mumara.com",
              "logo": "https://mumara-new.netlify.app/assets/img/logo-dark.png",
              "contactPoint": {
                "@type": "ContactPoint",
                "telephone": "+1 (844) 686 – 2721",
                "contactType": "customer service"
              },
              "sameAs": [
                "https://www.facebook.com/MumaraApps",
                "https://twitter.com/MumaraApps",
                "https://plus.google.com/+MumaraEmailMarketer",
                "https://www.youtube.com/channel/UC1k6voQOVss0_PWg7CfJTdw",
                "https://www.linkedin.com/company/mumara"
              ]
            }
          `}
        </script>
        <script type="application/ld+json">
          {`
            {
              "@context":"https://schema.org",
              "@type":"ItemList",
              "itemListElement":[
                {
                  "@type":"ListItem",
                  "position":1,
                  "url":"https://www.mumara.com/campaigns/"
                },
                {
                  "@type":"ListItem",
                  "position":2,
                  "url":"https://www.mumara.com/one/"
                },
                {
                  "@type":"ListItem",
                  "position":3,
                  "url":"https://www.mumara.com/sms/"
                },
                {
                  "@type":"ListItem",
                  "position":4,
                  "url":"https://www.mumara.com/smsplus/"
                },
                {
                  "@type":"ListItem",
                  "position":5,
                  "url":"https://www.mumara.com/classic/"
                }
              ]
            }
          `}
        </script>
        <script type="application/ld+json">
          {`
            {
              "@context": "https://schema.org",
              "@type": "Person",
              "name": "Mumara",
              "url": "https://www.mumara.com",
              "sameAs": [
                "https://www.facebook.com/MumaraApps",
                "https://twitter.com/MumaraApps",
                "https://plus.google.com/+MumaraEmailMarketer",
                "https://www.youtube.com/channel/UC1k6voQOVss0_PWg7CfJTdw",
                "https://www.linkedin.com/company/mumara"
              ]
            }
          `}
        </script>
        <script type="application/ld+json">
          {`
            {
              "@context": "https://schema.org/",
              "@type": "WebSite",
              "name": "Mumara",
              "url": "https://www.mumara.com/",
              "potentialAction": {
                "@type": "SearchAction",
                "target": "https://www.mumara.com/?s={s}",
                "query-input": "required name=s"
              }
            }
          `}
        </script>
        <script src="https://code.jquery.com/jquery-3.6.0.min.js" integrity="sha256-/xUj+3OJU5yExlq6GSYGSHk7tPXikynS7ogEvDej/m4=" crossorigin="anonymous"></script>
        <script data-ad-client="ca-pub-7570269198350551" async src="https://pagead2.googlesyndication.com/pagead/js/adsbygoogle.js"></script>
        <script id='sbinit' src='https://dashboard.mumara.com/js/init.js'></script>
        <script>
          {`
            window.ga=window.ga||function(){(ga.q=ga.q||[]).push(arguments)};ga.l=+new Date;
            ga('create', 'UA-63566119-1', 'auto');
            ga('send', 'pageview');
          `}
        </script>
        <script async src='https://www.google-analytics.com/analytics.js'></script>
        <script>
          {`
            !function(f,b,e,v,n,t,s)
            {if(f.fbq)return;n=f.fbq=function(){n.callMethod?
            n.callMethod.apply(n,arguments):n.queue.push(arguments)};
            if(!f._fbq)f._fbq=n;n.push=n;n.loaded=!0;n.version='2.0';
            n.queue=[];t=b.createElement(e);t.async=!0;
            t.src=v;s=b.getElementsByTagName(e)[0];
            s.parentNode.insertBefore(t,s)}(window, document,'script',
            'https://connect.facebook.net/en_US/fbevents.js');
            fbq('init', '329504770823436');
            fbq('track', 'Mumara PageView');
          `}
        </script>
        <noscript>
          {`<img height="1" width="1" style="display:none" src="https://www.facebook.com/tr?id=329504770823436&ev=ViewContent&noscript=1" />`}
        </noscript>
      </Helmet>
      <div className="sitesearch" itemscope itemtype="https://schema.org/WebSite" style={{display: "none"}}>
        <meta itemprop="url" content="https://www.mumara.com/"/>
        <form itemprop="potentialAction" itemscope itemtype="https://schema.org/SearchAction">
          <meta itemprop="target" content="https://www.mumara.com/?s={s}"/>
          <input itemprop="query-input" type="text" name="s" required/>
          <input type="submit"/>
        </form>
      </div>
      <span className="socialorg" itemscope itemtype="https://schema.org/Organization" style={{display: "none"}}>
        <link itemprop="url" href="https://www.mumara.com" />
        <a itemprop="sameAs" href="https://www.facebook.com/MumaraApps">FB</a>
        <a itemprop="sameAs" href="https://twitter.com/MumaraApps">Twitter</a>
        <a itemprop="sameAs" href="https://plus.google.com/+MumaraEmailMarketer">Google+</a>
        <a itemprop="sameAs" href="https://www.youtube.com/channel/UC1k6voQOVss0_PWg7CfJTdw">Youtube</a>
        <a itemprop="sameAs" href="https://www.linkedin.com/company/mumara">Linkedin</a>
      </span>
      <ol itemscope itemtype="http://schema.org/BreadcrumbList" style={{display: "none"}}>
        <li itemprop="itemListElement" itemscope
            itemtype="http://schema.org/ListItem">
          <a itemscope itemtype="http://schema.org/Thing"
            itemprop="item" href="https://www.mumara.com" id="https://www.mumara.com">
              <span itemprop="name">Mumara</span>
              <img itemprop="image" src="https://mumara-new.netlify.app/assets/img/logo-dark.png" alt="Mumara"/></a>
          <meta itemprop="position" content="1" />
        </li>
        ›
        <li itemprop="itemListElement" itemscope
            itemtype="http://schema.org/ListItem">
          <a itemscope itemtype="http://schema.org/Thing"
            itemprop="item" href="https://www.mumara.com/classic-esp/" id="https://www.mumara.com/classic-esp/">
            <span itemprop="name">Classic ESP</span>
            <img itemprop="image" src="https://mumara-new.netlify.app/assets/img/logo-dark.png" alt="SMS+ | Mumara"/></a>
          <meta itemprop="position" content="2" />
        </li>
      </ol>
      <div class="schemaproduct" itemscope itemtype="http://schema.org/Product" style={{display: "none"}}>
        <meta itemprop="mpn" content="925877" />
        <span itemprop="brand">Mumara</span>
        <span itemprop="name">Classic ESP</span>
        <img itemprop="image" src="https://mumara-new.netlify.app/assets/img/logo-dark.png" alt="Classic ESP" />
        <span itemprop="description">Email service providers can have the Mumara ESP installed on their webserver, can configure it with all required resources to offer email services to its clients.
        </span>
        Product #: <span itemprop="mpn">1</span>
        <span itemprop="aggregateRating" itemscope itemtype="http://schema.org/AggregateRating">
          <span itemprop="ratingValue">5.0</span> stars, based on <span itemprop="reviewCount">1077
            </span> reviews
        </span>
        <span itemprop="offers" itemscope itemtype="http://schema.org/Offer">
          <span itemprop="seller" itemscope itemtype="http://schema.org/Organization">
            <span itemprop="name">Hostings House</span>
          </span>
        <link itemprop="url" href="https://www.mumara.com/classic-esp/" />
          <meta itemprop="availability" content="https://schema.org/InStock" />
          <meta itemprop="priceCurrency" content="USD" />
          <meta itemprop="itemCondition" content="https://schema.org/UsedCondition" />
          <meta itemprop="price" content="47" />
          <meta itemprop="priceValidUntil" content="2021-11-20" />
        </span>
        <meta itemprop="sku" content="0446310777" />
        <div itemprop="review" itemtype="http://schema.org/Review" itemscope>
              <div itemprop="author" itemtype="http://schema.org/Person" itemscope>
                <meta itemprop="name" content="Wasif Ahmed" />
              </div>
              <div itemprop="reviewRating" itemtype="http://schema.org/Rating" itemscope>
                <meta itemprop="ratingValue" content="5.0" />
                <meta itemprop="bestRating" content="5" />
              </div>
        </div>
      </div>

    </Layout>
  )
}

export const query = graphql`
  query classicespBlockContent {
    allBlockContent(filter: { page: { in: ["classic-esp"] } }) {
      nodes {
        ...BlockContent
      }
    }
  }
`
export default IndexPage
